@import "../../styles/variables.scss";

header.main {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: solid 3px $primary-color;
  padding: 5px;

  svg {
    font-size: 2rem;
    margin: 0 10px;
    color: $primary-color;

    @include breakpoint-small {
      font-size: 60px;
    }

    @include breakpoint-large {
      font-size: 70px;
    }
  }

  div {
    font-family: $title-font;
    font-size: 2rem;
    color: $secondary-color;

    @include breakpoint-small {
      font-size: 36px;
    }

    @include breakpoint-large {
      font-size: 48px;
    }
  }
}
